  }

  .numbers__items {
    flex-direction: column;
    align-items: center;
  }

  .numbers__item {
    align-items: center;
    width: 100%;
  }

  .present__grid {
    grid-template-columns: repeat(1, minmax(300px, 1fr));
  }

  .present-slider {
    order: 1;
  }

  .reasons__items {
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
  }

  .reasons__item {
    align-items: center;
  }

  .reasons__item:first-child {
    width: 50%;
  }

  .ex__items {
    flex-direction: column;
    align-items: center;
  }

  .ex__item {
    width: 100%;
    align-items: center;
  }

  .video__heading {
    top: 30px;
  }

  .video__img-item {
    height: 100%;
  }

  .video__img-item {
    width: auto;
  }

  .video__play-icon {
    width: 50px;
    height: 50px;
  }

  .gallery__items {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 576px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }

  .container-full {
    padding-left: 10px;
    padding-right: 10px;
  }

  .organizers__items {
    flex-direction: column;
    justify-content: center;
  }

  .gallery__items {
    grid-template-columns: repeat(1, 1fr);
  }

  .footer__outer {
    grid-template-columns: repeat(1, 1fr);
  }
}

@media (hover: hover) {
  .f-button:hover:not([disabled]) {
    color: var(--f-button-hover-color);
    background-color: var(--f-button-hover-bg);
  }
}